import {mapState} from 'vuex';
import uniIcon from '@/components/uni-icon/uni-icon.vue'
import uniIconfont from '@/components/uni-iconfont/uni-icon.vue'
import {ajaxGetAddresses, ajaxDelAddresses} from '@/data/ajax.js';
import {turnPage} from '@/common/global.js';
export default {
	components: {
		uniIcon,
		uniIconfont
	},
	data() {
		return {
			parentPage: ''
		};
	},
	computed: {
		...mapState(['config', 'consignees'])
	},
	onLoad(e) {
		let _this = this;
		this.parentPage = e.from;
		// 获取收货地址信息
		ajaxGetAddresses(this.$store.state.user, (data) => {
			_this.$store.commit('setConsignee', data.result);
		});
	},
	methods: {
		clkAdd () {
			turnPage('csg-creat');
		},
		clkItem (index) {
			if (this.parentPage === 'car') {
				this.$store.commit('setFirstConsignee', index);	
				uni.navigateBack();
			}
		},
		clkDel (index) {
			var _this = this;
			
			uni.showModal({
				title: '提示',
				content: '确定删除该地址？',
				success: function (res) {
					if (res.confirm) {
						ajaxDelAddresses(_this.consignees[index], () => {
							uni.showToast({title: '地址已成功删除', position: 'bottom'});
							_this.$store.commit('deleteConsignee', index);
						});
					}
				}
			});
		},
		clkEdit (index) {
			turnPage('csg-creat', index);
		}
	}
};
